.new-collections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 100px;
    padding: 10px;
}
.new-collections h1{
    color: red;
    font-size: 50px;
    font-weight: 600;
    padding: 2px;
}
.new-collections hr{
    width: 200px;
    height: 6px;
    border-radius: 6px;
    background-color: goldenrod;
    padding: 2px;
}
.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 5px;
}