.Popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 1px;
    height: 90vh;
}
.Popular h1{
    color: aqua;
    font-size: 50px;
    font-weight: 600;
}
.Popular hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background-color: blueviolet;
}
.Popular-item{
    margin-top: 50px;
    display: flex;
}